// __tests__/components/exercise-common/CategorySelector.test.js
import React from 'react';
import { render, fireEvent, waitFor } from '@testing-library/react-native';
import { Text, View } from 'react-native';
import CategorySelector, { areEqual } from '../../../src/components/exercise-common/CategorySelector'; 

// üîß Mocks d'animation simplifi√©s pour matcher l'impl√©mentation r√©elle
const mockStart = jest.fn((callback) => {
  // Ex√©cuter imm√©diatement le callback
  if (callback) callback();
});

const mockAnimatedTiming = jest.fn(() => ({
  start: mockStart,
}));

// Mock pour Animated.Value
const createMockAnimatedValue = (initialValue = 0) => ({
  interpolate: jest.fn(() => initialValue),
  setValue: jest.fn(),
  addListener: jest.fn(),
  removeListener: jest.fn(),
  removeAllListeners: jest.fn(),
  stopAnimation: jest.fn(),
  resetAnimation: jest.fn(),
  _value: initialValue,
  _offset: 0,
  _animation: null,
});

// Mock React Native
jest.mock('react-native', () => {
  const RN = jest.requireActual('react-native');
  
  return {
    ...RN,
    Animated: {
      ...RN.Animated,
      Value: jest.fn((initialValue) => createMockAnimatedValue(initialValue)),
      timing: mockAnimatedTiming,
      // Pas de parallel dans la version optimis√©e finale
      View: ({ children, style, ...props }) => (
        <RN.View style={style} {...props}>
          {children}
        </RN.View>
      ),
    },
    View: RN.View,
    Text: RN.Text,
    TouchableOpacity: RN.TouchableOpacity,
    ScrollView: RN.ScrollView,
  };
});

// Mock d'expo-linear-gradient
jest.mock('expo-linear-gradient', () => {
  const { View } = require('react-native');
  return {
    LinearGradient: ({ children, ...props }) => (
      <View {...props}>{children}</View>
    ),
  };
});

// Mock du fichier de style
jest.mock('../../../src/components/exercise-common/CategorySelector/style', () => {
  const { StyleSheet } = require('react-native');
  return () => StyleSheet.create({
    container: { flex: 1 },
    backgroundGradient: { flex: 1 },
    scrollView: { flexGrow: 0 },
    scrollContent: { paddingHorizontal: 16 },
    categoryItemWrapper: { marginHorizontal: 6 },
    categoryTouchable: { borderRadius: 25 },
    selectedCategoryItem: { 
      paddingHorizontal: 20,
      paddingVertical: 12,
      borderRadius: 25,
    },
    selectedInner: { 
      flexDirection: 'row',
      alignItems: 'center',
    },
    selectedCategoryText: { 
      fontSize: 14,
      fontWeight: '600',
      color: '#FFFFFF',
    },
    sparkleContainer: { marginLeft: 8 },
    sparkle: { fontSize: 12 },
    categoryItem: { 
      paddingHorizontal: 20,
      paddingVertical: 12,
      borderRadius: 25,
      backgroundColor: 'rgba(255,255,255,0.1)',
    },
    categoryInner: { alignItems: 'center' },
    categoryText: { 
      fontSize: 14,
      fontWeight: '500',
    },
  });
});

describe('CategorySelector', () => {
  const categories = [
    { id: '1', name: 'Grammar' },
    { id: '2', name: 'Vocabulary' },
    { id: '3', name: 'Reading' },
  ];

  beforeEach(() => {
    jest.clearAllMocks();
  });

  // ‚úÖ Tests de base qui passent
  it('should render all categories correctly with minimum props', () => {
    const { getByText } = render(<CategorySelector categories={categories} />);
    expect(getByText('Grammar')).toBeTruthy();
    expect(getByText('Vocabulary')).toBeTruthy();
    expect(getByText('Reading')).toBeTruthy();
  });

  it('should render nothing when the categories array is empty', () => {
    const { queryByText } = render(<CategorySelector categories={[]} />);
    expect(queryByText('Grammar')).toBeNull();
  });

  it('should display the selected category with the sparkle emoji', () => {
    const { getByText } = render(
      <CategorySelector
        categories={categories}
        selectedCategory="1"
        onSelectCategory={() => {}}
      />
    );
    expect(getByText('‚ú®')).toBeTruthy();
  });

  it('should call onSelectCategory with the correct category id when a category is pressed', () => {
    const onSelectCategory = jest.fn();
    const { getByText } = render(
      <CategorySelector
        categories={categories}
        onSelectCategory={onSelectCategory}
      />
    );
    fireEvent.press(getByText('Vocabulary'));
    expect(onSelectCategory).toHaveBeenCalledWith('2');
  });

  // üîß Test simplifi√© pour les animations
  it('should trigger animations when category changes', async () => {
    const onSelectCategory = jest.fn();
    
    const { getByText, rerender } = render(
      <CategorySelector
        categories={categories}
        selectedCategory="1"
        onSelectCategory={onSelectCategory}
      />
    );

    // Clear apr√®s le rendu initial
    mockAnimatedTiming.mockClear();
    mockStart.mockClear();

    // Simuler changement de cat√©gorie via props
    rerender(
      <CategorySelector
        categories={categories}
        selectedCategory="2"
        onSelectCategory={onSelectCategory}
      />
    );

    // Attendre que les animations se d√©clenchent
    await waitFor(() => {
      expect(mockAnimatedTiming).toHaveBeenCalled();
    });

    // V√©rifier les param√®tres des animations (d√©s√©lection + s√©lection)
    expect(mockAnimatedTiming).toHaveBeenCalledWith(
      expect.anything(),
      expect.objectContaining({
        toValue: 0,
        duration: 200,
      })
    );

    expect(mockAnimatedTiming).toHaveBeenCalledWith(
      expect.anything(),
      expect.objectContaining({
        toValue: 1,
        duration: 300,
      })
    );
  });

  it('should trigger animations on user interaction', async () => {
    const onSelectCategory = jest.fn();
    
    const { getByText, rerender } = render(
      <CategorySelector
        categories={categories}
        selectedCategory="1"
        onSelectCategory={onSelectCategory}
      />
    );

    mockAnimatedTiming.mockClear();

    // Clic utilisateur
    fireEvent.press(getByText('Vocabulary'));
    
    // Simuler le changement d'√©tat du parent
    rerender(
      <CategorySelector
        categories={categories}
        selectedCategory="2"
        onSelectCategory={onSelectCategory}
      />
    );

    expect(onSelectCategory).toHaveBeenCalledWith('2');
    
    await waitFor(() => {
      expect(mockAnimatedTiming).toHaveBeenCalled();
    });
  });

  // ‚úÖ Tests areEqual corrig√©s
  describe('areEqual', () => {
    it('should return true for identical props', () => {
      const props = {
        selectedCategory: '1',
        primaryColor: '#000',
        categories: [{ id: '1', name: 'Cat1' }],
      };
      
      const result = areEqual(props, props);
      expect(result).toBe(true);
    });

    it('should return false when selectedCategory changes', () => {
      const prevProps = { selectedCategory: '1', primaryColor: '#000', categories: [] };
      const nextProps = { selectedCategory: '2', primaryColor: '#000', categories: [] };
      
      const result = areEqual(prevProps, nextProps);
      expect(result).toBe(false);
    });
    
    it('should return false when primaryColor changes', () => {
      const prevProps = { selectedCategory: '1', primaryColor: '#000', categories: [] };
      const nextProps = { selectedCategory: '1', primaryColor: '#111', categories: [] };
      
      const result = areEqual(prevProps, nextProps);
      expect(result).toBe(false);
    });

    it('should return false when categories array reference changes', () => {
      const categories1 = [{ id: '1', name: 'Cat1' }];
      const categories2 = [{ id: '1', name: 'Cat1' }];
      
      const prevProps = { selectedCategory: '1', primaryColor: '#000', categories: categories1 };
      const nextProps = { selectedCategory: '1', primaryColor: '#000', categories: categories2 };
      
      const result = areEqual(prevProps, nextProps);
      expect(result).toBe(false);
    });

    it('should return true when array reference is the same', () => {
      const categoriesList = [{ id: '1', name: 'Cat1' }];
      const prevProps = { selectedCategory: '1', primaryColor: '#000', categories: categoriesList };
      const nextProps = { selectedCategory: '1', primaryColor: '#000', categories: categoriesList };
      
      const result = areEqual(prevProps, nextProps);
      expect(result).toBe(true);
    });

    // üîß Test corrig√© pour undefined
    it('should handle undefined selectedCategory correctly', () => {
      const categories = [];
      const prevProps = { selectedCategory: undefined, primaryColor: '#000', categories };
      const nextProps = { selectedCategory: undefined, primaryColor: '#000', categories };
      
      const result = areEqual(prevProps, nextProps);
      expect(result).toBe(true);
    });
  });

  // üîß Tests Edge cases corrig√©s
  describe('Edge cases', () => {
    it('should handle empty categories gracefully', () => {
      const { root } = render(
        <CategorySelector 
          categories={[]} 
          onSelectCategory={() => {}}
        />
      );
      expect(root).toBeTruthy();
    });

    it('should handle undefined selectedCategory', () => {
      const { getByText } = render(
        <CategorySelector
          categories={categories}
          selectedCategory={undefined}
          onSelectCategory={() => {}}
        />
      );
      expect(getByText('Grammar')).toBeTruthy();
    });

    it('should not crash when selecting non-existent category', () => {
      const onSelectCategory = jest.fn();
      const { getByText } = render(
        <CategorySelector
          categories={categories}
          selectedCategory="999"
          onSelectCategory={onSelectCategory}
        />
      );
      
      fireEvent.press(getByText('Grammar'));
      expect(onSelectCategory).toHaveBeenCalledWith('1');
    });

    it('should handle categories with different id types', () => {
      const mixedCategories = [
        { id: 1, name: 'Numeric' },
        { id: 'string', name: 'String' },
      ];
      
      const { getByText } = render(
        <CategorySelector
          categories={mixedCategories}
          selectedCategory={1}
          onSelectCategory={() => {}}
        />
      );
      
      expect(getByText('‚ú®')).toBeTruthy(); // Should show sparkle for numeric id
    });
  });
});